import React from "react"
import Title from "./Title"
import { FaAngleDoubleRight } from "react-icons/fa"
import { graphql, useStaticQuery } from "gatsby"
import { Link } from "gatsby"

const Artworks = () => {
  const query = graphql`
  {
    allStrapiArtworks {
      nodes {
        id
        Title
        Designer
        Date
        Description
        additional_info {
          id
          text
        }
      }
    }
  }
`
  return <h2>Artworks</h2>
}

export default Artworks
